@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup {
    background-color: v.$rosa;
    padding: 5rem 0;

    h3, p {
        text-align: center;
        color: v.$blanco;
    }

    .dia {
        text-transform: uppercase;
        margin: 5rem 0;
    }
}

.escenarios-contenedor {
    @include m.tablet {
        @include m.grid (2, 5rem);
    }

    p {
        color: v.$negro;
    }

}

.escenario {
    padding-top: 2rem;

    p {
        &:after {
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2rem auto 0 auto;
        }
    }
}

.edm .nombre-escenario:after {
    background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
    background-image: url(../img/icono_rock.png);
}

.calendario {
    @include m.resetear-lista; //Esto es un mixins hecho en el archivo misins

    li {
        padding: 2.5rem;
        font-weight: bold;

        span {
            font-weight: normal;
        }

    }
}

.bg-amarillo {
    background-color: v.$amarillo;

    li:nth-child(odd) { //La propiedad odd seleccionará los hijos impares
        background-color: darken(v.$amarillo, 15%); //darken es una propiedad de sass, lo que hace es oscurecer un color, el % que se le indique
    }
}

.bg-verde {
    background-color: v.$verde;

    li:nth-child(odd) { //La propiedad odd seleccionará los hijos impares
        background-color: darken(v.$verde, 10%); //darken es una propiedad de sass, lo que hace es oscurecer un color, el % que se le indique
    }
}